.App{
    text-align: center;
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.head{
    color: navy;
}

.App h1{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-style: inherit;
}

.result{
    height : 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: whitesmoke;
    width: 100%;
    box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.5);

}

.result p{
    font-size: xx-large;
}

.calc-body{
    max-width: 400px;
    margin : auto
}

.buttons{
    display: block;
    background-color:black;
}

.buttons button{
    height:50px;
    width:100px;
    font-size: 20px;
    border:1px darkslategrey;
    background-color: lightsteelblue;
    box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.5);

}
